import validCode from "@/components/common/vaildCode.vue"
import uniStatusBar from "@/components/uni-ui/uni-status-bar/uni-status-bar.vue"
import commonButton from '@/components/common/common-button.vue';
import {mapMutations} from 'vuex';
export default {
	components: {
		validCode,uniStatusBar,commonButton
	}, 
	onLoad(e) {
		if(e.phone !== ''){
			this.username = e.phone
		}
		
	},
	data() {
		return {
			username:"",
			vaildcode:"",
			check:false,
			
			// 验证规则
			rules:{
				
				username:[
					{
						rule:/^1[3456789]\d{9}$/,
						msg:"手机号格式不正确"
					}
				]
			},
			
			focusClass:{
				username:false,
				vaild:false
			},
		}
	},
	methods: {
		...mapMutations(['login']),
		goBack(){
			uni.navigateBack({
				delta: 1
			});
		},
		getCode(val){
			this.vaildcode = val
		},
		openVaild(){
			uni.showModal({
				content:'默认为1234'
			})
		},
		// 表单验证
		validate(key){
			var check = true
			this.rules[key].forEach((v)=>{
				if (!(v.rule).test(this[key])) {
					uni.showToast(
						{ 
							title: v.msg, 
							icon: 'none'
						}
					)
					check = false
					return false
				}
			})
			if(this.vaildcode !== '1234')
			{
				uni.showToast(
					{ 
						title: '验证码错误', 
						icon: 'none'
					}
				)
				check = false
				return check
			}
			
			return check
		},
		submit(){
			if (!this.check) {
				return uni.showToast({
					title: '请先同意芒果点餐应用协议',
					icon: 'none'
				});
			}
			if (!this.validate('username')) return;
			
			uni.showLoading({
				title: '登录中...',
				mask: true
			})
			this.$H.post('/user/user/login/vaild',{
				username:this.username,
				password:this.vaildcode
			}).then(res=>{
				console.log(res)
				this.login(res)
				// 刷新购物车
				uni.$emit('updateCart')
				uni.hideLoading()
				uni.showToast({
					title: '登录成功',
					icon: 'none'
				});
				uni.navigateTo({
					url:'/pages/profile/profile'
				})
			}).catch(err=>{
				console.log(err)
			})
		},
		forget(){
			
		},
		focus(key){
			this.focusClass[key] = true
		},
		blur(key){
			this.focusClass[key] = false
		}
	}
}
